			<view v-if="xuanxiang == 1" class="dingwei_juedui  huxing_you " style="width: 100vw;height: calc(100vh - 20vw);z-index: 999;margin-top: 20vw;">
				<view class=" huxing_you donghua" style="width: 50vw;height: calc(100vh - 22vw);z-index: 999;background: #99cbef;left: 0vw;position: absolute;">
