					 :key="index" style="width: 20vw;text-align: center;margin-top: 2vw;margin-left: 4vw;" class="kuang huxing_quan">
						<view style="height: 12vw;font-size: 120%;line-height: 12vw;">{{item.usePeriod}}</view>
					</view>

				</view>

				<view style="border-radius:0 0px 0px 0;height: 10vw;line-height: 10vw;font-size: 128%;font-weight: 700;color: #1f1f29;padding-left: 3vw;background:#f3f3f3 ;">
					缴费项目
				</view>

				<view style="width: 100vw;padding-bottom: 2vw;" class="hengfang ">

					<view :class="item.isDefault==1?'xuanzhe_1':'xuanzhe_0'" @click="qiehuan_xuanzhong_leixing(index)" v-for="(item,index) in feiyong_type"
					 :key="index" style="text-align: center;margin-top: 2vw;margin-left: 4vw;padding-left: 2vw;padding-right: 2vw;"
					 class="kuang huxing_quan">
						<view style="height: 12vw;font-size: 120%;line-height: 12vw;">{{item.payitemName}}</view>
					</view>

				</view>

				<view style="border-radius:0 0px 0px 0;height: 10vw;line-height: 10vw;font-size: 128%;font-weight: 700;color: #1f1f29;padding-left: 3vw;background:#f3f3f3 ;">
					缴费房间
				</view>

				<view style="width: 100vw;padding-bottom: 2vw;" class="hengfang ">

					<view :class="item.isDefault==1?'xuanzhe_1':'xuanzhe_0'" @click="qiehuan_xuanzhong_fangjian(index)" v-for="(item,index) in fangjian_list"
					 :key="index" style="text-align: center;margin-top: 2vw;margin-left: 4vw;padding-left: 2vw;padding-right: 2vw;"
					 class="kuang huxing_quan">
						<view style="height: 12vw;font-size: 120%;line-height: 12vw;">{{item.roomName}}</view>
					</view>

				</view>


				<view class="dingwei_juedui dibu" style="width: 100vw;height: 14vw;background: #FFFFFF;">
					<view @click="liebiao_chongpai()" class="huxing_quan" style="margin-bottom: 2vw;margin-left: 5vw;width: 90vw;height: 10vw;background: #518bee;text-align: center;color: #FFFFFF;line-height: 10vw;font-size: 110%;margin-top: 2vw;"><text>确定</text></view>
				</view>

			</view>

			<view class="dingwei_juedui huxing_quan" style="margin-top: 45vw;width: 94vw;margin-left: 3vw;height: calc(100vh - 47vw);background: #FFFFFF;">
				<view style="width: inherit;height: calc(100vh - 63vw);overflow: auto;">

					<view v-if="feiyong_list.length<1" style="text-align: center;width: 100vw;margin-top: 40vw;">
						<image src="/static/img/gg_01.png" style="width: 30vw;height: 30vw;"></image>
						<view style="margin-top: 2vw;">没有记录</view>
					</view>

					<view v-for="(item,index) in feiyong_list" :key="index">
						<view @click="qiehuan_xuanzhong(index)" class="hengfang" style="min-height: 12vw;font-size: 120%;color: #454545;line-height: 7vw;">
							<view style="flex: 1;margin-left: 4vw;">
								<image v-if="item.isDefault == 1" src="/static/png/fxk_1.png" style="width: 10vw;height: 10vw;margin-top: 7vw;margin-right: 4vw;"></image>
								<image v-if="item.isDefault == 0" src="/static/png/fxk_0.png" style="width: 10vw;height: 10vw;margin-top: 7vw;margin-right: 4vw;"></image>
							</view>
							<view style="flex: 4;margin-left: 0vw;font-size: 90%;">
								<view>{{item.roomName}}</view>
								<view>{{item.usePeriod}}</view>
								<view>{{item.payitemName}}</view>
							  </view>
							<view style="flex: 2;margin-right: 5vw;text-align: right;margin-top: 14vw;">{{item.fee}}元</view>
						</view>
						<view style="width: 80vw;height: 5rpx;background: #f4f4f4;margin-left: 7vw;"></view>
					</view>

					<!-- 					<view v-if="xuanxiang == 1" v-for="(item,index) in nianyue_type" :key="index">
						<view @click="qiehuan_xuanzhong_nianyue(index)" class="hengfang" style="height: 12vw;font-size: 120%;color: #454545;line-height: 12vw;">
							<view style="flex: 1;margin-left: 8vw;">
								<image v-if="item.xuanzhong == 1" src="/static/png/fxk_1.png" style="width: 7vw;height: 7vw;margin-top: 2vw;"></image>
								<image v-if="item.xuanzhong == 0" src="/static/png/fxk_0.png" style="width: 7vw;height: 7vw;margin-top: 2vw;"></image>
							</view>
							<view style="flex: 4;margin-left: 0vw;">{{item.biaoti}}</view>
							<view style="flex: 3;margin-right: 5vw;text-align: right;"></view>
						</view>
						<view style="width: 80vw;height: 5rpx;background: #f4f4f4;margin-left: 7vw;"></view>
					</view> -->

					<!-- 					<view v-if="xuanxiang == 2" v-for="(item,index) in feiyong_type" :key="index">
						<view @click="qiehuan_xuanzhong_leixing(index)" class="hengfang" style="height: 12vw;font-size: 120%;color: #454545;line-height: 12vw;">
							<view style="flex: 1;margin-left: 8vw;">
								<image v-if="item.xuanzhong == 1" src="/static/png/fxk_1.png" style="width: 7vw;height: 7vw;margin-top: 2vw;"></image>
								<image v-if="item.xuanzhong == 0" src="/static/png/fxk_0.png" style="width: 7vw;height: 7vw;margin-top: 2vw;"></image>
							</view>
							<view style="flex: 4;margin-left: 0vw;">{{item.biaoti}}</view>
							<view style="flex: 3;margin-right: 5vw;text-align: right;"></view>
						</view>
						<view style="width: 80vw;height: 5rpx;background: #f4f4f4;margin-left: 7vw;"></view>
					</view> -->


				</view>

				<view @click="zhifu()" class="huxing_quan" style="margin-top: 5vw;margin-left: 5vw;width: 83vw;height: 10vw;background: #518bee;text-align: center;color: #FFFFFF;line-height: 10vw;font-size: 110%;"><text>立即缴费(￥{{jiner}})</text></view>
				<!-- <view v-if="xuanxiang > 0" @click="liebiao_chongpai()" class="huxing_quan" style="margin-top: 5vw;margin-left: 5vw;width: 83vw;height: 10vw;background: #518bee;text-align: center;color: #FFFFFF;line-height: 10vw;font-size: 110%;"><text>确定</text></view> -->

 
			</view>

		</view>

	</view>
</template>

<script>
	import debug from '../../../components/debug.vue';
	export default {
		components: {
			debug
		},
		data() {
			return {
				debug_show: getApp().globalData.public_tiaoshi_moshi,
				feiyong_list: [],
				jiner: 0,
				feiyong_type: [],
				xuanxiang: 0,
				yunsuan_list: [],
				nianyue_type: [],
				nianyue_paixu: 1, //1正排 2倒排
				xiangmu_paixu: 1, //1正排 2倒排
				fangjian_list: [],
				orderNumber: "",


			}
		},
		methods: {
			qiehuan_xuanzhong: function(res) {
				let _this = this
				_this.feiyong_list[res].isDefault = !_this.feiyong_list[res].isDefault
				_this.jisuan_zongjia()
				console.log(res)
			},
			qiehuan_xuanzhong_leixing: function(res) {
				let _this = this
				_this.feiyong_type[res].isDefault = !_this.feiyong_type[res].isDefault
			},
			qiehuan_xuanzhong_fangjian: function(res) {
				let _this = this
				_this.fangjian_list[res].isDefault = !_this.fangjian_list[res].isDefault
			},
			qiehuan_xuanzhong_nianyue: function(res) {
				let _this = this
				_this.nianyue_type[res].isDefault = !_this.nianyue_type[res].isDefault
			},
			jisuan_zongjia: function() {
				let _this = this
				let geshu = _this.feiyong_list.length
				let jiner = 0
				for (let i = 0; i < geshu; i++) {
					if (_this.feiyong_list[i].isDefault == 1) {
						jiner += parseFloat(_this.feiyong_list[i].fee)
					}
				}
				this.jiner = jiner.toFixed(2)
			},
			leixing_qiehuan: function(res) {
				if (this.xuanxiang == 0) {
					this.xuanxiang = res
				} else {
					this.xuanxiang = 0
				}
			},
			liebiao_chongpai: function() {
				let _this = this

				let xiangmu_xuanzhong_shuliang = 0
				let xiangmu_zongshu = _this.feiyong_type.length
				for (let i = 0; i < xiangmu_zongshu; i++) {
					if (_this.feiyong_type[i].isDefault == 1) {
						xiangmu_xuanzhong_shuliang += 1
					}
				}
				if (xiangmu_xuanzhong_shuliang < 1) {
					uni.showModal({
						title: '提示',
						content: '选择项目不能为空',
						showCancel: false,
						success: function() {}
					});
					return
				}

				let nianyue_xuanzhong_shuliang = 0
				let nianyue_zongshu = _this.nianyue_type.length
				for (let i = 0; i < nianyue_zongshu; i++) {
					if (_this.nianyue_type[i].isDefault == 1) {
						nianyue_xuanzhong_shuliang += 1
					}
				}
				if (nianyue_xuanzhong_shuliang < 1) {
					uni.showModal({
						title: '提示',
						content: '选择年月不能为空',
						showCancel: false,
						success: function() {}
					});
					return
				}

				let linshi_list = []

				if (_this.xiangmu_paixu == 1) {
					for (let i = 0; i < xiangmu_zongshu; i++) {
						if (_this.feiyong_type[i].isDefault == 1) {

							let shoufei_liebiao_shu = _this.yunsuan_list.length
							for (let j = 0; j < shoufei_liebiao_shu; j++) {
								if (_this.yunsuan_list[j].payitemId == _this.feiyong_type[i].payitemId)
									linshi_list.push(_this.yunsuan_list[j]);
							}

						}
					}
				} else {
					for (let i = xiangmu_zongshu; i > 0; i--) {
						if (_this.feiyong_type[i - 1].isDefault == 1) {
							let shoufei_liebiao_shu = _this.yunsuan_list.length
							for (let j = 0; j < shoufei_liebiao_shu; j++) {
								if (_this.yunsuan_list[j].payitemId == _this.feiyong_type[i - 1].payitemId)
									linshi_list.push(_this.yunsuan_list[j]);
							}

						}
					}
				}




				let linshi_list_2 = []

				if (_this.nianyue_paixu == 1) {
					for (let i = 0; i < nianyue_zongshu; i++) {
						if (_this.nianyue_type[i].isDefault == 1) {
							let nianyue_liebiao_shu = linshi_list.length
							for (let j = 0; j < nianyue_liebiao_shu; j++) {
								if (linshi_list[j].usePeriod == _this.nianyue_type[i].usePeriod)
									linshi_list_2.push(linshi_list[j]);
							}
						}
					}
				} else {
					for (let i = nianyue_zongshu; i > 0; i--) {
						if (_this.nianyue_type[i - 1].isDefault == 1) {
							let nianyue_liebiao_shu = linshi_list.length
							for (let j = 0; j < nianyue_liebiao_shu; j++) {
								if (linshi_list[j].usePeriod == _this.nianyue_type[i - 1].usePeriod)
									linshi_list_2.push(linshi_list[j]);
							}
						}
					}
				}


				let linshi_list_3 = [];
				let shuju_shu = linshi_list_2.length
				let fangjian_shu = _this.fangjian_list.length
				for (let i = 0; i < fangjian_shu; i++) {
					for (let j = 0; j < shuju_shu; j++) {
						if (_this.fangjian_list[i].isDefault == 1) {
							if (_this.fangjian_list[i].roomId == linshi_list_2[j].roomId) {
								linshi_list_3.push(linshi_list_2[j]);
							}
						}
					}
				}

				_this.feiyong_list = linshi_list_3
				_this.xuanxiang = 0
				_this.jisuan_zongjia()

			},
			qiehuan_nianyue_paixu: function() {
				let _this = this
				if (_this.nianyue_paixu == 1) {
					_this.nianyue_paixu = 2
				} else {
					_this.nianyue_paixu = 1
				}
				_this.liebiao_chongpai()
			},
			qiehuan_xiangmu_paixu: function() {
				let _this = this
				if (_this.xiangmu_paixu == 1) {
					_this.xiangmu_paixu = 2
				} else {
					_this.xiangmu_paixu = 1
				}
				_this.liebiao_chongpai()

			},
			huoqu_jiaofei_leixing: function() {
				let _this = this
				let data = {}
				_this.wangluo_qingqiu('/wx/ipms/getfeechargelogs', data, true, true, 'GET').then(value => {
					if (value) {
						_this.feiyong_type = value
					}
				})
			},
			huoqu_jiaofei_nianyue: function() {
				let _this = this
				let data = {}
				_this.wangluo_qingqiu('/wx/ipms/getipmsfeechargemoon', data, true, true, 'GET').then(value => {
					if (value) {
						_this.nianyue_type = value
					}
				})
			},
			huoqu_jiaofei_yuanshi: function() {
				let _this = this
				let data = {}
				_this.wangluo_qingqiu('/wx/ipms/getipmsfeecharge', data, true, true, 'GET').then(value => {
					if (value) {
						_this.yunsuan_list = value
						_this.feiyong_list = _this.yunsuan_list
						_this.jisuan_zongjia()
					}
				})
			},
			huoqu_jiaofei_fangjian: function() {
				let _this = this
				let data = {}
				_this.wangluo_qingqiu('/wx/ipms/getroomsbycustomerid', data, true, true, 'GET').then(value => {
					if (value) {
						_this.fangjian_list = value
					}
				})
			},
			zhifu: function() {

				let _this = this
				let jiaofei_id = []

				let feiyong_zongshu = _this.feiyong_list.length
				for (let i = 0; i < feiyong_zongshu; i++) {
					if (_this.feiyong_list[i].isDefault == 1) {
						jiaofei_id.push(_this.feiyong_list[i].chargeLogid);
					}
				}

				if (jiaofei_id.length == 0) {
					uni.showModal({
						title: '提示',
						content: '没有选择缴费项目',
						showCancel: false,
						success: function() {}
					});
					return false
				}

				let data = {
					ids: jiaofei_id,
				}
				_this.wangluo_qingqiu('/wx/pay/sendipmsfeecharge', data, true, true, 'POST').then(value => {
					if (value) {
						_this.orderNumber = value.orderNumber
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: value.timeStamp,
							nonceStr: value.nonceStr,
							package: value.package,
							signType: value.signType,
							paySign: value.paySign,
							success: function(res) {

								let data = {
									orderNumber: _this.orderNumber,
								}
								_this.wangluo_qingqiu('/wx/pay/paystate', data, true, true, 'GET').then(value => {
									if (value == 1) {
										uni.showModal({
											title: '提示',
											content: '缴费完成',
											showCancel: false,
											success: function() {
												_this.huoqu_jiaofei_leixing()
												_this.huoqu_jiaofei_nianyue()
												_this.huoqu_jiaofei_fangjian()
												_this.huoqu_jiaofei_yuanshi()
											}
										});
									} else {
										uni.showModal({
											title: '提示',
											content: '小程序支付成功，但是服务器却未收到款项。请重试',
											showCancel: false,
											success: function() {
												_this.huoqu_jiaofei_leixing()
												_this.huoqu_jiaofei_nianyue()
												_this.huoqu_jiaofei_fangjian()
												_this.huoqu_jiaofei_yuanshi()
											}
										});
									}
								});
							},
							fail: function(err) {
								uni.showModal({
									title: '注意',
									content: '取消支付。',
									confirmText: '知道了',
									success: res => {
										let data = {
											orderNumber: _this.orderNumber,
										}
										_this.wangluo_qingqiu('/wx/pay/payfailed', data, true, true, 'POST').then(value => {
											if (value) {
												_this.huoqu_jiaofei_leixing()
												_this.huoqu_jiaofei_nianyue()
												_this.huoqu_jiaofei_fangjian()
												_this.huoqu_jiaofei_yuanshi()
											}
										})

									}
								});
							}
						});


					}
				})



			}

		},
		onLoad: function() {
			if(getApp().globalData.public_tiaoshi_moshi){
				getApp().globalData.public_debug = []
			}
			let _this = this

			_this.huoqu_jiaofei_leixing()
			_this.huoqu_jiaofei_nianyue()
			_this.huoqu_jiaofei_fangjian()
			_this.huoqu_jiaofei_yuanshi()




		}
	}
</script>

